@import (less) 'color.less';

.sign-container {
  padding-top: 20px;
  padding-bottom: 20px;
}

.sign-customer-section {
  background-color: @white;
  min-height: 145px;
  border: 1px solid @light_gray;
}

.customer-section-title {
  font-size: 1em;
  font-weight: 700;
  padding-top: 24px;
}

.customer-section-info {
  font-size: 1em;
  padding-top: 6px;
}

.customer-circle-info {
  width: 40px;
  height: 40px;
  border: 1px solid @light_gray;
  border-radius: 40px;
  background: @white;
  position: absolute;
  top: -20px;
  text-align: center;
  font-weight: 700;
  padding-top: 8px;
  color: @light_green;
}

.sign-place-section {
  background-color: @lightest_gray;
  min-height: 145px;
  border-top: 1px solid @light_gray;
  border-bottom: 1px solid @light_gray;
  border-right: 1px solid @light_gray;
  border-left: 0px solid transparent;
  padding-top: 10px;
  &:before {
    content: '';
    display:block;
    width:20px;
    height:20px;  
    background-color: @lightest_gray;
    border-top: 1px solid @light_gray;
    border-left: 1px solid @light_gray;
    position:absolute;
    transform-origin:top left;
    transform:rotate(-45deg) translateX(-20px);
    top:34px;
    left:0px;    
  }
  
}

.sign-type-section {
  background-color: @dirt_gray;
  min-height: 145px;
  border-top: 1px solid @light_gray;
  border-bottom: 1px solid @light_gray;
  border-right: 1px solid @light_gray;
  border-left: 0px solid transparent;
  padding-top: 10px;
  &:before {
    content: '';
    display:block;
    width:20px;
    height:20px;  
    background-color: @dirt_gray;
    border-top: 1px solid @light_gray;
    border-left: 1px solid @light_gray;
    position:absolute;
    transform-origin:top left;
    transform:rotate(-45deg) translateX(-20px);
    top:34px;
    left:0px;
  }
}

::ng-deep nbp-radio .radio-label {
  font-weight: 300 !important;
  font-size: 1em !important;
  padding-top: 10px;
}

@media screen and (max-width: 767px) {
  .sign-place-section  {
    border-top: 0px solid transparent;
    border-left: 1px solid @light_gray !important;
    &:before {
      border-top: 1px solid @light_gray;
      border-right: 1px solid @light_gray;
      border-left: 0px solid transparent;
      top: -15px;
      left: 34px;
    }
  } 
  .sign-type-section {
    border-top: 0px solid transparent;
    border-left: 1px solid @light_gray !important;
    &:before {
      border-top: 1px solid @light_gray;
      border-right: 1px solid @light_gray;
      border-left: 0px solid transparent;
      top: -15px;
      left: 34px;
    }
  }
}
